.chat {
  display: flex;
  flex-direction: column;
  background-color: #d1c8d4;
  height: 790px;
  overflow-y: scroll;

  /* height: 790px; */
}

.chat_window1 {
  margin-top: 45px;
  margin-left: 350px;
  margin-bottom: 20px;
  display: flex;
  width: 334px;

  background-color: #fff;
  border-radius: 13px;
  align-items: center;
}

.chat_window2 {
  margin-top: 45px;
  margin-left: 25px;
  margin-bottom: 20px;
  display: flex;
  width: 334px;

  background-color: #e3dae6;
  border-radius: 13px;
  align-items: center;
}
.msg {
  padding: 20px 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: #033772;
}
.input {
  position: relative;
  box-sizing: border-box;
  padding: 30px 0 40px 40px;
  border-bottom: none;
  border-left: none;
  border-right: none;
  background-color: #efe6f2;
  width: 100%;
  height: 150px;
  color: #033772;
}
textarea::-webkit-input-placeholder {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: rgba(3, 55, 114, 0.5);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}
textarea {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  resize: none;
}

.input:focus {
  outline: none;
}

.sendMsg {
  position: relative;
  padding: 10px;
  right: -625px;
  top: -50px;
  background-color: transparent;
  border: none;
}
